import styles from "../../support/help-templates/css/help-templates.css";
import { helpTemplates } from "../../support/helpTemplates";
import { RFxArguments, RFxTemplate } from "../../support/interfaces";
import {
  DefaultLocale,
  SupportedLocales,
  fetchCommonStrings,
  fetchHelpStrings,
  formatLocalizedHelpUrl,
  getDirectionBasedOnRTL,
  getLocaleInfo
} from "../../support/localization";
import { helpDoc } from "../../support/resources";
import { appConfig } from "../../support/appConfig";

import { getFunctionName, getFunctionsAndCategories, getRFTFunctionIdentifier } from "./functions";
import { isEmptyDataItem } from "./type-gaurds";
import { getRFTName, isSingleArgumentFunction } from "./rfx-args-helper";

function createHelpTemplate({
  content,
  locale = DefaultLocale,
  dir = "ltr"
}: {
  content: string;
  locale?: SupportedLocales;
  dir?: string;
}) {
  // create the HTML string using template literals
  const html = `
      <html lang="${locale}" dir="${dir}">
        <head><style>${styles}</style></head>
        <body>
          ${content}
        </body>
      </html>
    `;
  return html;
}

async function getRfxFunctionByName(rfxName: string) {
  const { rfxFunctions } = await getFunctionsAndCategories();
  return rfxFunctions.find((rfx) => rfx.name === rfxName);
}

/**
 * Replaces the parameter keys in a response text (HTML) with localized help strings and formats the help HTML.
 * @param {string} responseText - The original response text/HTML containing parameter keys.
 * @param {object} localizedHelpText - An object containing localized help text strings for the parameter keys.
 * @returns {string} The response text/HTML with parameter keys replaced with localized help strings and formatted help text.
 */
function replaceHelpTextKeys(responseText: string, localizedHelpText): string {
  // use regex to replace all parameter keys with localized strings
  const keyRegex = /@@(.*?)@@/gm;
  const responseTextHTML = responseText.replace(keyRegex, function (match) {
    const helpText = localizedHelpText[match];
    // replace "--" with "  "
    const helpContent = helpText.replace(/--/gm, "  ");
    // replace all user text in ${(.*?)} pattern with the text inside
    const textRegex = /\${(.*?)}/gm;
    return helpContent.replace(textRegex, "$1");
  });

  // add blank space around text in between <strong></strong>
  const strongRegex = /<strong>(.*?)<\/strong>/gm;
  const helpTextHTML = responseTextHTML.replace(strongRegex, " <strong>$1</strong> ");

  return helpTextHTML;
}

export async function getHelpText(helpTextID: string) {
  if (isEmptyDataItem(helpTextID)) {
    return;
  }

  try {
    const nlsHelpTextKey = helpTextID;

    // // fetch template html content for parameter help tooltip
    const helpTemplate = helpTemplates?.[nlsHelpTextKey];

    // return helpTemplate && nlsHelpTextKey;
    if (isEmptyDataItem(helpTemplate)) {
      throw Error("No helpTemplate found");
    }

    const { locale, rtl } = getLocaleInfo();
    const dir = getDirectionBasedOnRTL(rtl);
    const helpStrings = await fetchHelpStrings(locale);
    const localizedHelpText = helpStrings.rfxArgsHelpTexts;
    const helpTextHTML = replaceHelpTextKeys(helpTemplate, localizedHelpText).replace(
      "<html>",
      `<html lang="${locale}" dir="${dir}">`
    );
    return helpTextHTML;
  } catch (e) {
    return createHelpTemplate({ content: "" });
  }
}

/**
 * TODO: remove and combine with getHelpText
 * Fetches help text for a given parameter of a function of type LocalFunction.
 * @async
 * @param {string} paramKey - The key of the parameter to get help text for.
 * @param {RFxArguments} rfxArgs - The rfxArgs to identify which function it is, to get help text from.
 * @param {RFxTemplate} rfxTemplate - The raster function template to get help text from.
 * @returns {Promise<string>} The help text HTML for the parameter, or undefined if not found.
 */
export async function getHelpTextForLocalFunctions(
  paramKey: string,
  rfxArgs?: RFxArguments,
  rfxTemplate?: RFxTemplate
) {
  if (isEmptyDataItem(paramKey) || isEmptyDataItem(rfxArgs.type)) {
    return;
  }

  try {
    // parse paramKey to paramName for help doc lookup
    let paramName = paramKey
      .trim()
      .toLowerCase()
      .replace(/_|-|\s/gm, "");

    // There is no way to identify RFT type of single argument functions using rfxArgs.
    // use rfxTemplate for these.
    let fnName =
      (isSingleArgumentFunction(rfxArgs)
        ? await getRFTName(rfxTemplate)
        : getFunctionName(rfxArgs).toLowerCase()?.replace("function", "")) ?? "";
    const specialCaseMap = helpDoc.rfxNameSpecialCases;

    const specialCase = specialCaseMap.find((sc) => sc.key === fnName.toLowerCase());
    fnName = specialCase?.value || fnName;

    // console.log(`${fnName.trim().toLowerCase().replace(/-|\s/gm, "")}_${paramName}`)

    // handle special case of interpolaterasterbydimension - variables
    if (fnName === "interpolaterasterbydimension" && paramKey === "DimensionDefinition") {
      paramName = "variables";
    }

    // Handle NDVI Colorized
    if ((fnName === "colormap" || fnName === "ndvi") && isEmptyDataItem(rfxArgs.customFunction) !== true) {
      fnName = rfxArgs.customFunction;
    }

    // Handle AggMdim
    if (
      (fnName === "rastercollection" || fnName === "multidimensionalfilter" || fnName === "cellstatistics") &&
      isEmptyDataItem(rfxArgs.customFunction) !== true
    ) {
      fnName = rfxArgs.customFunction;
    }

    const nlsHelpTextKey = `${fnName.trim().toLowerCase().replace(/-|\s/gm, "")}_${paramName}`;

    // fetch template html content for parameter help tooltip
    const helpTemplate = helpTemplates?.[nlsHelpTextKey];

    if (isEmptyDataItem(helpTemplate)) {
      throw Error("No helpTemplate found");
    }

    const { locale, rtl } = getLocaleInfo();
    const dir = getDirectionBasedOnRTL(rtl);
    const helpStrings = await fetchHelpStrings(locale);
    const localizedHelpText = helpStrings.rfxArgsHelpTexts;
    const helpTextHTML = replaceHelpTextKeys(helpTemplate, localizedHelpText).replace(
      "<html>",
      `<html lang="${locale}" dir="${dir}">`
    );
    return helpTextHTML;
  } catch (e) {
    return createHelpTemplate({ content: "" });
  }
}

export async function getFunctionDescriptionHelpTemplate({
  rfxName,
  rfxTemplate,
  portal
}: {
  rfxName: string;
  rfxTemplate: RFxTemplate;
  portal: __esri.Portal;
}) {
  let helpTemplate,
    learnMoreUrl,
    functionIdentifier = rfxName;

  const { locale, rtl } = getLocaleInfo();
  const dir = getDirectionBasedOnRTL(rtl);

  const helpBase = portal?.sourceJSON?.helpBase || appConfig.learnMoreDocBaseURL;

  try {
    const isRFT = !isEmptyDataItem(rfxTemplate);

    // RFTs have a valid "sourceItemId" while history items don't.
    const isHistoryItem = isRFT && isEmptyDataItem(rfxTemplate?.sourceItemId);
    if (isHistoryItem) {
      functionIdentifier = getRFTFunctionIdentifier(rfxTemplate);
    }

    if (isRFT && !isHistoryItem) {
      const functionDescription = rfxTemplate.description || "";
      // done to bypass type-error.
      // eslint-disable-next-line @typescript-eslint/dot-notation
      const portalItemURL = `${portal?.["itemPageUrl"]}?id=${rfxTemplate.sourceItemId}`;

      helpTemplate = createHelpTemplate({ content: functionDescription, locale, dir });
      learnMoreUrl = portalItemURL;
    } else {
      const rfxFunction = await getRfxFunctionByName(functionIdentifier);

      const functionDescription = rfxFunction?.description || "";
      const functionHelpID = rfxFunction?.helpID ?? appConfig.learnMoreDocDefaultHelpID;

      helpTemplate = createHelpTemplate({ content: functionDescription, locale, dir });
      learnMoreUrl = formatLocalizedHelpUrl(functionHelpID, helpBase, locale);
    }
  } catch (e) {
    learnMoreUrl = formatLocalizedHelpUrl(appConfig.learnMoreDocDefaultHelpID, helpBase, locale);
    helpTemplate = createHelpTemplate({ content: "", locale, dir });
  }

  return { helpTemplate, learnMoreUrl };
}

export async function getOutputRasterHelpTemplate(helpId: string) {
  const { locale, rtl } = getLocaleInfo();
  const dir = getDirectionBasedOnRTL(rtl);
  const helpStrings = await fetchCommonStrings(locale);
  const localizedHelpText = helpStrings.rasterFunctions.outputRasterHelpTexts;
  const helpTextHTML = localizedHelpText[helpId];

  const helpTemplate = createHelpTemplate({ content: helpTextHTML, locale, dir });
  return helpTemplate;
}

export function getHelpTemplateForInfoMessage(info: string) {
  const { locale, rtl } = getLocaleInfo();
  const dir = getDirectionBasedOnRTL(rtl);
  const helpTextHTML = `<p>${info}</p>`;
  const helpTemplate = createHelpTemplate({ content: helpTextHTML, locale, dir });
  return helpTemplate;
}
