import * as geoprocessor from "@arcgis/core/rest/geoprocessor.js";
import esriRequest from "@arcgis/core/request.js";
import PortalItem from "@arcgis/core/portal/PortalItem.js";

import { RFxTemplate } from "../../support/interfaces";

import { isEmptyDataItem } from "./type-gaurds";

const convertRFTToolName = "ConvertRasterFunctionTemplate";

const rasterUtilitiesHelperService =
  "https://rasteranalysisdev.arcgis.com/arcgis/rest/services/Utilities/RasterUtilities/GPServer";

function isXMLTemplateItem(item) {
  const name = item.name,
    format = name && name.slice(-8),
    isXML = format && format.toLowerCase() === ".rft.xml";
  return isXML;
}

async function getRFTJson(url): Promise<RFxTemplate> {
  try {
    const result = await esriRequest(url, {
      query: { f: "json" }
    });
    return result.data as RFxTemplate;
  } catch {
    Promise.reject("fetching rft data from url failed");
  }
}

function getConvertRFTHelperService(portal) {
  const helperServices = portal.helperServices,
    rasterUtilitiesHelperServiceFromPortal = helperServices && helperServices.rasterUtilities;
  return `${
    rasterUtilitiesHelperServiceFromPortal ? rasterUtilitiesHelperServiceFromPortal.url : rasterUtilitiesHelperService
  }`;
}

async function getConvertRFTToJson(rftItem): Promise<RFxTemplate> {
  const convertRFTParams = {
    format: "json",
    inputRasterFunction: JSON.stringify({
      itemId: rftItem.id
    })
  };
  const rasterUtilitiesServer = getConvertRFTHelperService(rftItem.portal);
  if (isEmptyDataItem(rasterUtilitiesServer)) {
    Promise.reject("rasterUtilities server missing");
  }
  let url;
  try {
    const jobInfo = await geoprocessor.submitJob(`${rasterUtilitiesServer}/${convertRFTToolName}`, convertRFTParams);
    const options = {
      interval: 1500,
      statusCallback: (j) => {
        console.log("status: ", j.jobStatus);
      }
    };
    await jobInfo.waitForJobCompletion(options);
    const parameterValue = await jobInfo.fetchResultData("outputRasterFunction");
    const dataFile = parameterValue.value;
    ({ url } = dataFile as __esri.DataFile);
  } catch {
    Promise.reject("json conversion failed");
  }
  return getRFTJson(url);
}

export async function getRFTJsonFromPortalItem(rftId: string, portal: __esri.Portal): Promise<RFxTemplate> {
  let rfxTemplate;
  const portalItem = new PortalItem({
    id: rftId ?? "b57c3ced6108427faabed74d8b567f96",
    portal: portal
  });
  await portalItem.load();
  const { title: name, snippet, id: sourceItemId } = portalItem;
  const isXML = isXMLTemplateItem(portalItem);
  if (!isXML) {
    const itemDataUrl = `${portalItem.itemUrl}/data`;
    rfxTemplate = await getRFTJson(itemDataUrl);
  } else {
    rfxTemplate = await getConvertRFTToJson(portalItem);
  }
  // updating the JSON with item details
  const updatedRFTJSON = { ...rfxTemplate, name, description: snippet ?? rfxTemplate.description, sourceItemId };
  return updatedRFTJSON;
}
