import { OutputName, RFxTemplate } from "../../support/interfaces";
import { SupportedLocales, fetchCommonStrings } from "../../support/localization";
import { dataTypes as DATA_TYPES, domainTypes as DOMAIN_TYPES } from "../../support/resources";
import { getSchema } from "../../support/schema";
import { RFxArgumentSchema, RFxSchema } from "../../support/schemaInterfaces";

import { ARGS_KEYWORD, RFT_TYPE, RFV_TYPE, TYPE_KEYWORD, isEmptyDataItem, isRFxArg } from "./type-gaurds";
import {
  canBuildArgRFTUI,
  containsArgName,
  getArgRFT,
  getCaseInsensitiveArg,
  getEnumData,
  getFunctionSchema,
  getRFTName,
  hasRasterElements,
  shouldHonorIsPublic
} from "./rfx-args-helper";
import { getLayerUrlWithoutToken } from "./layer-helper";
import { getFunctionsAndCategories } from "./functions";
/**
 * Get a flat list of all parameters from RFT.
 * @param {RFxTemplate} rfxTemplate The RFT to be parsed.
 * @returns {any[]} The flat list containing all parameters in RFT.
 */
function getRFTParamList(rfxTemplate: RFxTemplate): any[] {
  const paramList = [];
  const rfxArgs = rfxTemplate[ARGS_KEYWORD];

  if (!isEmptyDataItem(rfxArgs)) {
    if (isRFxArg(rfxArgs)) {
      // for functions with only one raster param, rfxArgs is the single raster arg
      paramList.push(rfxArgs);
    } else {
      Object.keys(rfxArgs).forEach((rfxTemplateArgKey) => {
        if (rfxTemplateArgKey !== TYPE_KEYWORD) {
          const rfxTemplateArg = rfxArgs[rfxTemplateArgKey];
          if (isRFxArg(rfxTemplateArg)) {
            paramList.push(rfxTemplateArg);
          }
        }
      });
    }
  }
  return paramList;
}

/**
 * Get the list of parameters with active state.
 * @param {any[]} paramList The input paramList to be processed.
 * @param {HashMap<RFxArgumentSchema>} functionSchemaArgs The args from function schema.
 * @returns {any[]} The flat list containing all parameters in RFT.
 */
function getActiveStateParamList(paramList: any[], functionSchemaArgs: HashMap<RFxArgumentSchema>): any[] {
  const paramsToHide = [];

  // same logic to handle active state triggers from schema
  paramList.forEach((param) => {
    const schemaArg = getCaseInsensitiveArg(param.name, functionSchemaArgs);
    if (isEmptyDataItem(schemaArg?.editorStateTrigger)) {
      return;
    }

    let argValue = param.value;
    if (schemaArg.dataType !== DATA_TYPES.boolean && !isNaN(argValue)) {
      // convert number strings to numbers
      argValue = Number(argValue);
    }
    const isStateTriggerAcive = schemaArg.editorStateTrigger.active;
    if (isEmptyDataItem(isStateTriggerAcive) || !isStateTriggerAcive) {
      return;
    }

    schemaArg.editorStateTrigger.triggers?.forEach((trigger: any) => {
      let isActive, isInactive, hideParam;

      // checkValuePresent flag: checks if a value is present in field,
      // if true and value is present set `isTriggerValue` as true. (used in `Switchable` Domain type)
      const autoRevert = trigger.autoRevert || false;
      const isInTriggerValue = trigger.checkValuePresent
        ? !isEmptyDataItem(argValue)
        : Array.isArray(trigger.values) && trigger.values.indexOf(argValue) >= 0;

      // display is only modified when either `isInTriggerValue`: true or `autoRevert`: true
      // if both are false then no need to iterate over each param as display wont be affected
      if (!isInTriggerValue && !autoRevert) {
        // skip to the next trigger
        return;
      }

      paramList.forEach((_param) => {
        if (param.name === _param.name) {
          return;
        }
        isActive = containsArgName(trigger.active, _param.name);
        isInactive = containsArgName(trigger.inactive, _param.name);
        hideParam = (isInactive && isInTriggerValue) || (isActive && !isInTriggerValue && autoRevert);
        if (hideParam) {
          paramsToHide.push(_param.name);
        }
      });
    });
  });

  return paramList.filter((param) => paramsToHide.indexOf(param.name) < 0);
}

/**
 * Filter and return parameters which are set as isPublic.
 * @param {boolean} honorIsPublic Whether to hornor isPublic flags in RFT params.
 * @param {any[]} rftParamList The parameter list from RFT to be filtered.
 * @returns {any[]} The filtered flat list containing all isPublic parameters.
 */
function getPublicParamList(honorIsPublic: boolean, rftParamList: any[]): any[] {
  return honorIsPublic ? rftParamList.filter((param) => param.isPublic === true) : rftParamList;
}

/**
 * Get the display name from rfx param.
 * @param param to get display value from.
 * @param {any} schemaArg arg schema info.
 * @returns the localized param display name.
 */
function getRFxParamDisplayName(param: any, schemaArg: any): string {
  // TODO: handle override widgets case
  return schemaArg?.displayName ?? param.displayName ?? param.name;
}

/**
 * Get the display name of raster param in local function rasters.
 * @param {any} param The local raster param to get display name from.
 * @param {number} index The index of raster within rasters array.
 * @param {any} schemaArg arg schema info.
 * @param {number} localOperationValue The operation value of local function.
 * @returns {string} The display name of local raster param.
 */
function getLocalRasterParamDisplayName(
  param: any,
  index: number,
  schemaArg: any,
  localOperationValue: number
): string {
  const { elementInfos } = schemaArg;
  let paramDisplayName = param.name;
  if (!isEmptyDataItem(elementInfos)) {
    const inputArgs = elementInfos.find(
      (elementInfo) => elementInfo.values.indexOf(localOperationValue) > -1
    )?.inputArgs;
    if (!isEmptyDataItem(inputArgs)) {
      paramDisplayName = inputArgs[index]?.displayName;
    }
  }
  return paramDisplayName;
}

/**
 * Get the display value of raster param.
 * @param {any} param The raster param to get display value from.
 * @param {any} rfxArgsStrings The strings used for translation.
 * @returns {any} The display value of raster param.
 */
function getRasterParamDisplayValue(param: any, rfxArgsStrings: any): any {
  const paramValue = param?.value;
  let paramDisplayValue = paramValue;
  if (param?.type === RFV_TYPE) {
    if (paramValue?.type === "Scalar") {
      paramDisplayValue = paramValue?.value ?? "0";
    } else if (!isEmptyDataItem(paramValue)) {
      const { name, url } = paramValue;
      const sanitizedUrl = getLayerUrlWithoutToken(url);
      paramDisplayValue = { name: name, url: sanitizedUrl };
    }
  } else if (param?.type === RFT_TYPE) {
    // handle nested RFT chain case in raster param
    const functionName = param?.function?.name ?? param?.name ?? "";
    paramDisplayValue = `<${functionName}.${rfxArgsStrings.outputRaster}>`;
  }
  return paramDisplayValue;
}

/**
 * Get the display value of raster array param.
 * @param {any} param The raster array param to get display value from.
 * @param {any} rfxArgsStrings The strings used for translation.
 * @returns {any} The display value of raster array param.
 */
function getRasterArrayParamDisplayValue(param: any, rfxArgsStrings: any): any {
  const paramValue = param?.value;
  const elements = !isEmptyDataItem(paramValue)
    ? paramValue?.elements ?? (Array.isArray(paramValue) ? paramValue : [paramValue])
    : [];
  const paramDisplayValue = Array.from(elements).map((element) => getRasterParamDisplayValue(element, rfxArgsStrings));
  return paramDisplayValue;
}

/**
 * Get the display value of feature class param.
 * @param {any} param The feature class param to get display value from.
 * @returns {any} The display value of feature class param.
 */
function getFeatureClassParamDisplayValue(param: any): any {
  const paramValue = param?.value;
  let paramDisplayValue = paramValue;
  if (!isEmptyDataItem(paramValue)) {
    const { name, url } = paramValue;
    const sanitizedUrl = getLayerUrlWithoutToken(url);
    paramDisplayValue = { name: name, url: sanitizedUrl };
  }

  return paramDisplayValue;
}

/**
 * Get the display value of color ramp datatype param.
 * @param {any} param The color ramp param to get display value from.
 * @returns {any} The display value of color ramp param.
 */
function getColorRampParamDisplayValue(param: any, rfxArgsStrings: any): any {
  const paramValue = param?.value;
  // for R02 release, only show name of colorramp, or "Custom" if no name
  const colorRampName = paramValue?.Name ?? paramValue?.name ?? undefined;
  const paramDisplayValue = !isEmptyDataItem(colorRampName) ? colorRampName : rfxArgsStrings.custom;
  return paramDisplayValue;
}

/**
 * Get the display value from rfx param.
 * @param param to get display value from.
 * @param {RFxArgumentSchema} schemaArg arg schema info.
 * @param {any} rfxArgsStrings strings used for translation.
 * @returns the localized param display value.
 */
function getRFxParamDisplayValue(param: any, schemaArg: RFxArgumentSchema, rfxArgsStrings: any): any {
  const paramValue = param?.value;
  let paramDisplayValue = paramValue;

  if (schemaArg !== undefined && schemaArg !== null) {
    switch (schemaArg.dataType) {
      case DATA_TYPES.boolean:
        // show localized string of "True" or "False" for boolean dataType
        paramDisplayValue = paramValue ? rfxArgsStrings.true : rfxArgsStrings.false;
        break;
      case DATA_TYPES.colorRamp:
        paramDisplayValue = getColorRampParamDisplayValue(param, rfxArgsStrings);
        break;
      case DATA_TYPES.featureClass:
        paramDisplayValue = getFeatureClassParamDisplayValue(param);
        break;
      case DATA_TYPES.raster:
        paramDisplayValue = getRasterParamDisplayValue(param, rfxArgsStrings);
        break;
      case DATA_TYPES.rasterArray:
        paramDisplayValue = getRasterArrayParamDisplayValue(param, rfxArgsStrings);
        break;
      default:
        break;
    }

    if (!isEmptyDataItem((schemaArg.domain as any)?.enum)) {
      // return corresponding enum label for enum domain type params value.
      const rfxArgEnum = getEnumData((schemaArg.domain as any)?.enum);
      paramDisplayValue = rfxArgEnum?.find((enumOption) => enumOption.key === paramValue?.toString())?.label;
    }

    if (!isEmptyDataItem(schemaArg.domain?.type) && schemaArg.domain?.type === DOMAIN_TYPES.bandIndex) {
      // make bandIndex domain type param values consistent with UI, add 1 as it is not 0-based.
      paramDisplayValue = !isEmptyDataItem(paramValue) ? (paramValue as number) + 1 : paramValue;
    }
  } else {
    // for param with modified names and does not have a matched schemaArg
    if (hasRasterElements(param)) {
      paramDisplayValue = getRasterArrayParamDisplayValue(param, rfxArgsStrings);
    } else {
      paramDisplayValue = getRasterParamDisplayValue(param, rfxArgsStrings);
    }
  }

  return paramDisplayValue ?? paramValue;
}

/**
 * Process the param list from rft and return localized json format results for history display.
 * @param {boolean} honorIsPublic Whether to hornor isPublic flags in RFT params.
 * @param {RFxTemplate} rfxTemplate The raster function template to be processed.
 * @param {HashMap<RFxSchema>} rasterFunctions The HashMap containing all RFx Schema info.
 * @param {any} rfxArgsStrings strings used for translation.
 * @returns The localized result json objs to be displayed on history details.
 */
function getLocalizedRFTParamListJSON(
  honorIsPublic: boolean,
  rfxTemplate: RFxTemplate,
  rasterFunctions: HashMap<RFxSchema>,
  rfxArgsStrings: any
): {
  translatedKeys: Record<string, string>;
  translatedJobParams: Record<string, any>;
} {
  let translatedKeys: Record<string, string> = {},
    translatedJobParams: Record<string, any> = {};

  const paramList = getRFTParamList(rfxTemplate);

  const indexesToSplice: number[] = [];

  paramList?.forEach((rfxArg, index) => {
    if (canBuildArgRFTUI(rfxArg)) {
      //handle custom RFT with nested function chain case
      let argRFT = getArgRFT(rfxArg);
      if (argRFT) {
        const { translatedKeys: translatedArgRFTKeys, translatedJobParams: translatedArgRFTJobParams } =
          getLocalizedRFTParamListJSON(honorIsPublic, argRFT, rasterFunctions, rfxArgsStrings);
        translatedKeys = { ...translatedKeys, ...translatedArgRFTKeys };
        translatedJobParams = { ...translatedJobParams, ...translatedArgRFTJobParams };

        // after the rfxArg with nested function chain has been parsed, mark index to remove it from the paramList
        indexesToSplice.push(index);
      } else if (hasRasterElements(rfxArg)) {
        const elements = rfxArg.value.elements ?? [rfxArg.value];
        Array.from(elements).forEach((element) => {
          argRFT = getArgRFT(element);
          if (argRFT) {
            const { translatedKeys: translatedArgRFTKeys, translatedJobParams: translatedArgRFTJobParams } =
              getLocalizedRFTParamListJSON(honorIsPublic, argRFT, rasterFunctions, rfxArgsStrings);
            translatedKeys = { ...translatedKeys, ...translatedArgRFTKeys };
            translatedJobParams = { ...translatedJobParams, ...translatedArgRFTJobParams };
          }
        });
      }
    }
  });

  if (indexesToSplice.length > 0) {
    indexesToSplice.sort(function (a, b) {
      return b - a;
    });
    // remove element(s) from paramList by indexes in descending order
    indexesToSplice.forEach((spliceIndex) => {
      paramList.splice(spliceIndex, 1);
    });
  }

  const functionSchema = getFunctionSchema({
    schema: rasterFunctions,
    rfxTemplate
  });

  const functionSchemaArgs = functionSchema?.rasterFunctionArguments;
  // TODO: handle schema editor overrides
  // const schemaEditorOverrides = functionSchema?.editorArgumentOverride?.active
  //   ? functionSchema.editorArgumentOverride.overrides
  //   : null;

  const activeStateParamList = getActiveStateParamList(paramList, functionSchemaArgs);
  const publicParamList = getPublicParamList(honorIsPublic, activeStateParamList);

  // return results of jobParams in a display-friendly format.
  publicParamList?.forEach((param) => {
    let paramName = param.name;
    let isLocalFunctionRasters = false;
    let localOperationValue;

    if (rfxTemplate?.function?.type === "LocalFunction" && paramName === "_Rasters") {
      // for local functions rasters input, use "Rasters" to get schemaArg
      paramName = "Rasters";
      isLocalFunctionRasters = true;
      localOperationValue = rfxTemplate.arguments?.Operation?.value;
    }

    const schemaArg = getCaseInsensitiveArg(paramName, functionSchemaArgs);
    if (schemaArg?.hidden !== true) {
      if (isLocalFunctionRasters) {
        param.value?.elements?.forEach((element, index) => {
          translatedKeys[element.name] = getLocalRasterParamDisplayName(element, index, schemaArg, localOperationValue);
          translatedJobParams[element.name] = getRasterParamDisplayValue(element, rfxArgsStrings);
        });
      } else {
        translatedKeys[paramName] = getRFxParamDisplayName(param, schemaArg);
        translatedJobParams[paramName] = getRFxParamDisplayValue(param, schemaArg, rfxArgsStrings);
      }
    }
  });
  return { translatedKeys, translatedJobParams };
}

/**
 * Parse the outputName JSON from jobParams and return localized json format results for history display.
 * @param {string} outputName The outputName JSON to be parsed.
 * @param {any} mainGenStrings The mainGenStrings for localization.
 * @returns The localized result json objs to be displayed on history details.
 */
function getTranslatedOutputName(
  outputName: string,
  mainGenStrings: any
): {
  translatedOutputNameKeys: Record<string, string>;
  translatedOutputNameJobParams: Record<string, any>;
} {
  const translatedOutputNameKeys: Record<string, string> = {},
    translatedOutputNameJobParams: Record<string, any> = {};

  const outputNameObj: OutputName = JSON.parse(outputName);

  translatedOutputNameKeys.outputName = mainGenStrings.outputName;
  translatedOutputNameJobParams.outputName =
    outputNameObj?.serviceProperties?.name ?? outputNameObj.itemProperties?.title ?? undefined;

  translatedOutputNameKeys.outputType = mainGenStrings.outputLayerType;
  translatedOutputNameJobParams.outputType =
    outputNameObj?.serviceProperties?.capabilities === "Image"
      ? mainGenStrings.dynamicImageryLayer
      : mainGenStrings.tiledImageryLayer;

  translatedOutputNameKeys.saveInFolder = mainGenStrings.saveInFolder;
  translatedOutputNameJobParams.saveInFolder = outputNameObj?.itemProperties?.folderId;

  return { translatedOutputNameKeys, translatedOutputNameJobParams };
}

/**
 * Format the RFT parameters and return a translated json format result for history display.
 * @param {RFxTemplate} rfxTemplate The RFT to be formatted.
 * @param {SupportedLocales} locale The locale used for translation.
 * @returns The promise of result json objs to be displayed on history details.
 */
export async function getTranslatedRFTParameters(
  rfxTemplate: RFxTemplate,
  outputName: string,
  locale?: SupportedLocales
): Promise<{
  translatedKeys: Record<string, string>;
  translatedJobParams: Record<string, any>;
}> {
  if (isEmptyDataItem(rfxTemplate) || isEmptyDataItem(locale)) {
    return { translatedKeys: {}, translatedJobParams: {} };
  }

  const commonStrings = await fetchCommonStrings(locale);
  let rfxArgsStrings = commonStrings.rasterFunctions.rfxArgs;
  const rfxArgsEditorStrings = commonStrings.rasterFunctionEditor.rfxArgsEditor;
  const mainGenStrings = commonStrings.rasterFunctions.mainGenStrings;
  rfxArgsStrings = { ...rfxArgsStrings, ...rfxArgsEditorStrings };
  rfxArgsStrings = { ...rfxArgsStrings, ...mainGenStrings };

  const rasterFunctions: HashMap<RFxSchema> = await getSchema(locale);

  const honorIsPublic = shouldHonorIsPublic(rfxTemplate);

  let { translatedKeys, translatedJobParams } = getLocalizedRFTParamListJSON(
    honorIsPublic,
    rfxTemplate,
    rasterFunctions,
    rfxArgsStrings
  );

  if (!isEmptyDataItem(outputName)) {
    const { translatedOutputNameKeys, translatedOutputNameJobParams } = getTranslatedOutputName(
      outputName,
      mainGenStrings
    );
    translatedKeys = { ...translatedKeys, ...translatedOutputNameKeys };
    translatedJobParams = { ...translatedJobParams, ...translatedOutputNameJobParams };
  }

  return { translatedKeys, translatedJobParams };
}

/**
 * Get translated rft name from rfxTemplate.
 * @param {RFxTemplate} rfxTemplate to get the name from.
 * @param {SupportedLocales} locale The locale used for translation.
 * @returns The promise of translated rft name.
 */
export async function getTranslatedRFTName(rfxTemplate: RFxTemplate, locale?: SupportedLocales): Promise<string> {
  if (isEmptyDataItem(rfxTemplate) || isEmptyDataItem(locale)) {
    return;
  }

  const commonStrings = await fetchCommonStrings(locale);
  const rfxStrings = commonStrings.rasterFunctions.rfx;

  const rasterFunctions = await getSchema(locale);
  const functionSchema = getFunctionSchema({
    schema: rasterFunctions,
    rfxTemplate: rfxTemplate
  });

  const rfxTemplateName = rfxTemplate.name;
  let translatedRFTName = rfxTemplateName;
  let translatedFunctionName = functionSchema.name;

  if (rfxTemplateName.toLowerCase() === rfxTemplate.function?.name?.toLowerCase()) {
    // return translated name for system function case; skip for custom RFTs
    if (isEmptyDataItem(translatedFunctionName)) {
      // for case like local functions, match function name in common strings
      const functionName = await getRFTName(rfxTemplate);
      translatedFunctionName =
        rfxStrings[Object.keys(rfxStrings).find((key) => key.toLowerCase() === functionName?.toLowerCase() + "name")];
    }

    if (!isEmptyDataItem(translatedFunctionName)) {
      translatedRFTName = translatedFunctionName;
    }
  }
  return translatedRFTName;
}

/**
 * Get translated system function names map from RFTDefinition.
 * @returns {Promise<Map<string, string>>} The promise of translated system function names map.
 */
export async function getTranslatedRFTNamesMap(): Promise<Map<string, string>> {
  const translatedRFTNamesMap: Map<string, string> = new Map();
  const { rfxFunctions } = await getFunctionsAndCategories();
  for (const rfxFunction of rfxFunctions) {
    if (!isEmptyDataItem(rfxFunction)) {
      translatedRFTNamesMap.set(rfxFunction.name, rfxFunction.title);
    }
  }
  return translatedRFTNamesMap;
}
