import esriRequest from "@arcgis/core/request";

import { isEqual, template, uniqueId } from "lodash-es";

import { LocaleItem } from "../../support/localization";

/**
 * Generates and returns a message with the keys in the input statement replaced with new values.
 * @param message
 * @param statement
 * @param parameters
 */
export function formatMessage(statement: Readonly<string>, parameters: Readonly<LocaleItem>): string {
  const templateFn = template(statement);
  return templateFn(parameters);
}

export function getUniqueId(prefix?: string): string {
  return uniqueId(prefix);
}

export function isObjectEqual(val1, val2) {
  return isEqual(val1, val2);
}

export function filterUniqueObjects(arr, property) {
  const uniqueValues = new Set();
  return arr.filter((item) => {
    if (!uniqueValues.has(item[property])) {
      uniqueValues.add(item[property]);
      return true;
    }
    return false;
  });
}

/**
 * Wrapper for the esriRequest
 * @param url the url
 * @param options the response type and other options
 * @returns RequestResponse
 */
export async function request(url: string, options: __esri.RequestOptions): Promise<__esri.RequestResponse> {
  return esriRequest(url, options);
}

/**
 * Get the difference between two arrays of objects
 * @param array1 object array
 * @param array2 object array
 * @param value property to compare
 * @returns object array
 */
export function arrayDifference(array1, array2, value): object[] {
  //Find values that are in result1 but not in result2
  const uniqueResultOne = array1.filter((obj) => !array2.some((obj2) => obj[value] == obj2[value]));

  //Find values that are in result2 but not in result1
  const uniqueResultTwo = array2.filter((obj) => !array1.some((obj2) => obj[value] == obj2[value]));

  //Combine the two arrays of unique entries
  return uniqueResultOne.concat(uniqueResultTwo);
}

// https://stackoverflow.com/questions/23223718/failed-to-execute-btoa-on-window-the-string-to-be-encoded-contains-characte
// solution for browser without using unescape:
export function toBinary(str) {
  let result = "";

  str = encodeURIComponent(str);

  for (let i = 0; i < str.length; i++)
    if (str[i] == "%") {
      result += String.fromCharCode(parseInt(str.substring(i + 1, i + 3), 16));
      i += 2;
    } else result += str[i];

  return result;
}
