import { SupportedLocales, getLocaleInfo } from "./localization-helper";

export async function fetchCommonStrings(
  locale: string = getLocaleInfo().locale
): Promise<typeof import("../../assets/t9n/common-strings.en.json")> {
  let strings = (await import("../../assets/t9n/common-strings.en.json")) as any;
  switch (locale) {
    case SupportedLocales.Arabic:
      strings = await import("../../assets/t9n/common-strings.ar.json");
      break;
    case SupportedLocales.Bosnian:
      strings = await import("../../assets/t9n/common-strings.bs.json");
      break;
    case SupportedLocales.Bulgarian:
      strings = await import("../../assets/t9n/common-strings.bg.json");
      break;
    case SupportedLocales.Catalan:
      strings = await import("../../assets/t9n/common-strings.ca.json");
      break;
    case SupportedLocales.Czech:
      strings = await import("../../assets/t9n/common-strings.cs.json");
      break;
    case SupportedLocales.Danish:
      strings = await import("../../assets/t9n/common-strings.da.json");
      break;
    case SupportedLocales.German:
      strings = await import("../../assets/t9n/common-strings.de.json");
      break;
    case SupportedLocales.Greek:
      strings = await import("../../assets/t9n/common-strings.el.json");
      break;
    // TODO: previously not supported by fn-editor
    // case SupportedLocales.EnglishUSA:
    //   strings = await import("./assets/common-strings.en-US.json");
    //   break;
    case SupportedLocales.Spanish:
      strings = await import("../../assets/t9n/common-strings.es.json");
      break;
    case SupportedLocales.Estonian:
      strings = await import("../../assets/t9n/common-strings.et.json");
      break;
    case SupportedLocales.French:
      strings = await import("../../assets/t9n/common-strings.fr.json");
      break;
    case SupportedLocales.Finnish:
      strings = await import("../../assets/t9n/common-strings.fi.json");
      break;
    case SupportedLocales.Hebrew:
      strings = await import("../../assets/t9n/common-strings.he.json");
      break;
    case SupportedLocales.Croatian:
      strings = await import("../../assets/t9n/common-strings.hr.json");
      break;
    case SupportedLocales.Hungarian:
      strings = await import("../../assets/t9n/common-strings.hu.json");
      break;
    case SupportedLocales.Indonesian:
      strings = await import("../../assets/t9n/common-strings.id.json");
      break;
    case SupportedLocales.Italian:
      strings = await import("../../assets/t9n/common-strings.it.json");
      break;
    case SupportedLocales.Japanese:
      strings = await import("../../assets/t9n/common-strings.ja.json");
      break;
    case SupportedLocales.Korean:
      strings = await import("../../assets/t9n/common-strings.ko.json");
      break;
    case SupportedLocales.Lithuanian:
      strings = await import("../../assets/t9n/common-strings.lt.json");
      break;
    case SupportedLocales.Latvian:
      strings = await import("../../assets/t9n/common-strings.lv.json");
      break;
    case SupportedLocales.NorwegianBokml:
      strings = await import("../../assets/t9n/common-strings.nb.json");
      break;
    case SupportedLocales.Dutch:
      strings = await import("../../assets/t9n/common-strings.nl.json");
      break;
    case SupportedLocales.Polish:
      strings = await import("../../assets/t9n/common-strings.pl.json");
      break;
    case SupportedLocales.PortugueseBrazil:
      strings = await import("../../assets/t9n/common-strings.pt-BR.json");
      break;
    case SupportedLocales.PortuguesePortugal:
      strings = await import("../../assets/t9n/common-strings.pt-PT.json");
      break;
    case SupportedLocales.Romanian:
      strings = await import("../../assets/t9n/common-strings.ro.json");
      break;
    case SupportedLocales.Russian:
      strings = await import("../../assets/t9n/common-strings.ru.json");
      break;
    case SupportedLocales.Serbian:
      strings = await import("../../assets/t9n/common-strings.sr.json");
      break;
    case SupportedLocales.Slovak:
      strings = await import("../../assets/t9n/common-strings.sk.json");
      break;
    case SupportedLocales.Slovenian:
      strings = await import("../../assets/t9n/common-strings.sl.json");
      break;
    case SupportedLocales.Swedish:
      strings = await import("../../assets/t9n/common-strings.sv.json");
      break;
    case SupportedLocales.Thai:
      strings = await import("../../assets/t9n/common-strings.th.json");
      break;
    case SupportedLocales.Turkish:
      strings = await import("../../assets/t9n/common-strings.tr.json");
      break;
    case SupportedLocales.Ukrainian:
      strings = await import("../../assets/t9n/common-strings.uk.json");
      break;
    case SupportedLocales.Vietnamese:
      strings = await import("../../assets/t9n/common-strings.vi.json");
      break;
    case SupportedLocales.ChineseChina:
      strings = await import("../../assets/t9n/common-strings.zh-CN.json");
      break;
    case SupportedLocales.ChineseHongKong:
      strings = await import("../../assets/t9n/common-strings.zh-HK.json");
      break;
    case SupportedLocales.ChineseTaiwan:
      strings = await import("../../assets/t9n/common-strings.zh-TW.json");
      break;
    case SupportedLocales.English:
    default:
      break;
  }
  return strings;
}

