import { SupportedLayerTypes } from "../../support/interfaces";

import { arrayDifference } from "./common";

/**
 * Check if the layer is a preview layer
 * @param layer supported layer type
 * @returns boolean
 */
export function isPreviewLayer(layer): boolean {
  return !!(layer.raster && layer.url?.toLowerCase().includes("/rasterrendering/imageserver"));
}

/**
 * check if the changed map layer is a preview layer
 * @param newMapLayers array of supported layer types
 * @param oldMapLayers array of supported layer types
 * @returns boolean
 */
export function isChangedMapLayerAPreview(
  newMapLayers: SupportedLayerTypes[],
  oldMapLayers: SupportedLayerTypes
): boolean {
  const changedMapLayer = arrayDifference(newMapLayers, oldMapLayers, "id");
  return changedMapLayer?.length === 1 && isPreviewLayer(changedMapLayer[0]);
}
